import { Component, OnInit } from '@angular/core';
import { BaseDirective } from '../../shared/globals/base.directive';
import { AppService } from '../../services/app.service';

@Component({
    selector: 'app-reports',
    templateUrl: './reports.template.html',
    styleUrls: ['./reports.styles.scss']
})
export class AppReportsComponent extends BaseDirective implements OnInit {
    public model: any = {};

    constructor(private service: AppService) {
        super();
    }

    public ngOnInit() {
        this.service.title = 'Reports';
        this.init();
    }

    public init() {
        if (!this.service.ready()) {
            return this.timeout('init', () => this.init());
        }
        this.service.set('APP.title', 'Select a category to view workplace information or run a report.');
        this.model.report_list = [
            // { id: 'booking', name: 'Booking Report', image: 'assets/img/report/booking.svg', details: 'Generate report on booking clashes and other complications' },
            // { id: 'visitor', name: 'Visitor Report', image: 'assets/img/report/visitor.svg', details: 'Generate report on visitor attendance' },
            // { id: 'transition', name: 'Transition Report', image: 'assets/img/report/transition.svg', details: 'Generate report on booking clashes and other complications' },
            {
                id: 'bookings',
                name: 'Bookings',
                icon: { type: 'img', src: 'assets/img/report/booking.svg' },
                details: 'Generate comprehensive report on various aspects of bookings'
            },
            {
                id: 'catering',
                name: 'Catering',
                icon: { class: 'material-icons', content: 'room_service' },
                details: 'View confirmed catering report or mark up unconfirmed catering'
            }
        ];
    }

    public goto(item: any) {
        if (item.id) {
            this.service.navigate(['reports', item.id], item.query || {});
        } else if (item.link || item.url) {
            window.open(item.link || item.url, 'blank_');
        }
    }
}
